import React from "react";
import { Home } from "./Components/Home";
import { Movie } from "./Components/Movie";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import "../node_modules/bootswatch/dist/lux/bootstrap.css";

function App() {
  return (
    <Router basename="/Movie">
      <div className="container text-center mx-auto mb-3 pb-5">
        <h4 className="mt-4">Movie search</h4>
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/:name" exact component={Movie} />
        </Switch>
      </div>
      <footer className=" footer bg-primary">
        <h6 className="mt-3 py-2 text-center text-white">
          Built with{" "}
          <span role="img" aria-label="heart">
            ❤️
          </span>{" "}
          by jana
        </h6>
      </footer>
    </Router>
  );
}

export default App;
