import React, { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
export function Home() {
  const [Input, setInput] = useState("");
  const [Movie, setMovie] = useState([]);
  const [error, setError] = useState(false);
  const [load, setLoad] = useState(false);
  const API_KEY = process.env.REACT_APP_API_KEY;
  const handleSearch = e => {
    e.preventDefault();
    setError(false);
    setLoad(true);
    handleFetch();
    setInput("");
  };
  const handleFetch = async () => {
    await axios
      .get(`https://www.omdbapi.com/?apikey=${API_KEY}&s=${Input}&type=movie`)
      .then(resp => {
        if (resp.data.Response === "False") {
          setError(true);
        } else {
          setMovie(resp.data.Search);
        }
      });
    setTimeout(() => {
      setLoad(false);
    }, 3000);
  };

  return (
    <React.Fragment>
      <form onSubmit={handleSearch} className="mt-3  mx-auto text-center">
        <input
          type="input"
          placeholder="Enter Movie Name"
          value={Input}
          required
          autoFocus
          className="form-control p-2  mx-auto text-center"
          onChange={e => {
            setInput(e.target.value);
          }}
        />
        <button type="submit" className="btn btn-primary btn-sm my-2 ">
          Search
        </button>
      </form>

      {error ? (
        <h4 className="bg-danger text-center">Enter valid movie name</h4>
      ) : load ? (
        <h4 className="loading m-5">
          Loading
          <span />
        </h4>
      ) : (
        <div className="grid my-4 ">
          {Movie.map((movie, index) => (
            <div className="card shadow p-3 card-size" key={index}>
              <h5 className="py-2">{movie.Title}</h5>
              {movie.Poster !== "" && (
                <img
                  src={movie.Poster}
                  alt={movie.Title}
                  className="img-thumbnail  rounded py-2 mx-auto"
                  width="200px"
                  height="200px"
                />
              )}
              <h6 className="text-secondary py-1">{movie.Year}</h6>
              <Link to={`/${movie.imdbID}`}>
                <h6 className="btn btn-primary btn-sm">Info</h6>
              </Link>
            </div>
          ))}
        </div>
      )}
    </React.Fragment>
  );
}
