import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
export function Movie({ match }) {
  const API_KEY = process.env.REACT_APP_API_KEY;

  // const [name, setName] = useState("");
  const [load, setLoad] = useState(true);
  const [data, setData] = useState({
    Ratings: []
  });
  const id = `https://www.omdbapi.com/?apikey=${API_KEY}&i=${
    match.params.name
  }`;
  useEffect(() => {
    // setName(match.params.name);
    handleFetchId();
    setTimeout(() => {
      setLoad(false);
    }, 3000);
  });

  const handleFetchId = async () => {
    axios.get(id).then(resp => setData(resp.data));
  };

  return (
    <React.Fragment>
      {load ? (
        <h4 className="loading m-5">
          Loading
          <span />
        </h4>
      ) : (
        <div className="my-3 p-2">
          <div className="card shadow py-2 text-center">
            <h6 className="my-2">{data.Title}</h6>
            <img
              src={data.Poster}
              alt={data.Title}
              className="img-thumbnail mx-auto my-2"
              width="200px"
              height="200px"
            />
            <h6 className="text-secondary my-2">{data.Year}</h6>
            <div className="row my-3">
              {data.Ratings.map((rate, index) => (
                <div className="col p-1" key={index}>
                  <h6>{rate.Source}</h6>
                  {rate.Value}
                </div>
              ))}
            </div>
            <hr />
            <div className="row">
              <div className="col p-1">
                <h6>Released</h6>
                {data.Released}
              </div>
              <div className="col p-1">
                <h6>Runtime</h6>
                {data.Runtime}
              </div>
              <div className="col p-1">
                <h6>Genre</h6>
                {data.Genre}
              </div>
            </div>
            <hr />
            <div className="my-2">
              <h6>plot</h6>
              <p className="lead px-5">{data.Plot}</p>
            </div>
            <div className="my-2 px-2">
              <h6>Actors</h6>
              <h6 className="text-secondary px-2">{data.Actors}</h6>
            </div>
            <div className="my-2 px-2 ">
              <h6>writer</h6>
              <h6 className="text-secondary">{data.Writer}</h6>
            </div>
            <div className="my-2 px-2">
              <h6>director</h6>
              <h6 className="text-secondary">{data.Director}</h6>
            </div>
            <div className="my-2 px-2">
              <h6>box office</h6>
              <h6 className="text-secondary">{data.BoxOffice}</h6>
            </div>
            <div className="my-2 px-2">
              <h6>awards</h6>
              <h6 className="text-secondary">{data.Awards}</h6>
            </div>
          </div>
          <Link to="/">
            <h6 className="btn btn-primary btn-sm my-2">back</h6>
          </Link>
        </div>
      )}
    </React.Fragment>
  );
}
